<template>
    <v-row no-gutters class="fill-height pa-2" align="start" justify="center">
        <v-col>
            <pui-table
                    :patients="patients"
                    :loading="loading"
                    v-on:view-pui="onViewPui"
                    v-on:view-lab="onViewLab"
                    v-on:view-hospitalization="onViewHospitalization"
                    v-on:add-pui="onAddPui"
            ></pui-table>
        </v-col>
        <v-dialog v-if="dialog_pui_view" v-model="dialog_pui_view" :max-width="768">
            <pui-view-form
                    :patient_id="pui_view_form.patient_id"
                    :ph_code="pui_view_form.ph_code"
                    :wv_code="pui_view_form.wv_code"
                    :sex="pui_view_form.sex"
                    :age="pui_view_form.age"
                    :province="pui_view_form.province"
                    :citymun="pui_view_form.citymun"
                    :places_visited="pui_view_form.places_visited"
                    :date_illness_onset="pui_view_form.date_illness_onset"
                    :signs_and_symptoms="pui_view_form.signs_and_symptoms"
                    :date_consulted="pui_view_form.date_consulted"
                    v-on:pui-edit="onEditPui"
                    v-on:dialog-close="dialog_pui_view = false"
            ></pui-view-form>
        </v-dialog>
        <v-dialog v-if="dialog_lab_view" v-model="dialog_lab_view" :max-width="500">
            <pui-lab-result-table
                    :patient_id="pui_lab_view.id"
                    v-on:lab-results-updated="onLabResultsUpdated"
                    v-on:dialog-close="dialog_lab_view = false"
            ></pui-lab-result-table>
        </v-dialog>
        <v-dialog v-if="dialog_hospitalization_view" v-model="dialog_hospitalization_view" :max-width="500">
            <pui-hospitalization-table
                    :patient_id="pui_hospitalization_view.id"
                    v-on:hospitalizations-updated="onHospitalizationsUpdated"
                    v-on:dialog-close="dialog_hospitalization_view = false"
            ></pui-hospitalization-table>
        </v-dialog>
        <v-dialog v-if="dialog_pui_add" v-model="dialog_pui_add" :max-width="768">
            <pui-add-form
                    v-on:dialog-close="dialog_pui_add = false"
                    v-on:pui-added="onPuiAdded">
            </pui-add-form>
        </v-dialog>
        <v-dialog v-if="dialog_pui_edit" v-model="dialog_pui_edit" :max-width="768">
            <pui-edit-form
                    :patient_id="pui_edit_form.patient_id"
                    v-on:dialog-close="dialog_pui_edit = false"
                    v-on:pui-edited="onPuiEdited">
            </pui-edit-form>
        </v-dialog>
    </v-row>
</template>

<script>
    import PuiTable from "../components/Pui/PuiTable"
    import PuiViewForm from "../components/Pui/PuiViewForm"
    import PuiLabResultTable from "../components/Pui/PuiLabResultTable"
    import PuiHospitalizationTable from "../components/Pui/PuiHospitalizationTable"
    import PuiAddForm from "../components/Pui/PuiAddForm"
    import PuiEditForm from "../components/Pui/PuiEditForm"
    import {getPatients, getPatientById, getPatientsPaged} from "../util/api"

    export default {
        name: "PUI",
        components: {
            PuiTable,
            PuiViewForm,
            PuiLabResultTable,
            PuiHospitalizationTable,
            PuiAddForm,
            PuiEditForm
        },
        created() {

            this.loadApi()
        },
        data() {
            return {
                loading: false,
                patients: [],
                totalPatients: 0,
                itemPerPage: 10,
                sortBy: 'id',
                direction: 'asc',
                page: 1,
                dialog_pui_view: false,
                dialog_lab_view: false,
                dialog_hospitalization_view: false,
                dialog_pui_add: false,
                dialog_pui_edit: false,
                pui_view_form: {
                    patient_id: '',
                    ph_code: '',
                    wv_code: '',
                    sex: '',
                    age: 0,
                    province: '',
                    citymun: '',
                    places_visited: '',
                    date_illness_onset: '',
                    signs_and_symptoms: '',
                    date_consulted: ''
                },
                pui_lab_view: {
                    id: 0
                },
                pui_hospitalization_view: {
                    id: 0
                },
                pui_edit_form: {
                    patient_id: '',
                }
            }
        },
        methods: {
            loadApi() {
                this.loading = true
                // const {page, itemsPerPage, sortBy, direction} = this

                //getPatientsPaged(page, itemsPerPage, sortBy, direction).then(res => {
                getPatients().then(res => {
                    this.patients = res.data.data
                    // this.totalPatients = res.data.meta.total
                    this.loading = false
                }).catch(err => {
                    console.log(err)
                })
            },
            // onTableOptionsUpdated(options) {
            //     console.log(options)
            //
            //     this.page = options.page
            //     this.itemsPerPage = options.itemsPerPage
            //     this.sortBy = options.sortBy
            //     this.direction = options.direction
            //
            //     this.loadApi()
            // },
            onViewPui(item) {
                const {id, ph_code, wv_code, sex, age, provDesc, citymunDesc, places_visited, date_illness_onset, signs_and_symptoms, date_consulted} = item
                this.pui_view_form = {
                    patient_id: id,
                    ph_code,
                    wv_code,
                    sex,
                    age,
                    province: provDesc,
                    citymun: citymunDesc,
                    places_visited,
                    date_illness_onset,
                    signs_and_symptoms,
                    date_consulted
                }

                this.dialog_pui_view = true
            },
            onEditPui(patient_id) {
                this.pui_edit_form.patient_id = patient_id
                this.dialog_pui_edit = true
            },
            onViewLab(id) {
                this.pui_lab_view.id = id
                this.dialog_lab_view = true
            },
            onViewHospitalization(id) {
                this.pui_hospitalization_view.id = id
                this.dialog_hospitalization_view = true
            },
            onAddPui() {
                this.dialog_pui_add = true
            },
            onPuiAdded(pui) {
                //old logic FOR NONE PAGED rest request
                //this.patients.push(pui)

                //FOR PAGED LOGIC, just refresh the current page, because it's not sure that the new model will be in the same page being viewed
                this.loadApi()
            },
            onPuiEdited(pui) {
                this.onViewPui(pui)
                this.onPatientUpdated(pui.id)
            },
            onLabResultsUpdated(patient_id) {
                this.onPatientUpdated(patient_id)
                //TODO add addtional snackbar or alert dialog to inform user
            },
            onHospitalizationsUpdated(patient_id) {
                this.onPatientUpdated(patient_id)
                //TODO add addtional snackbar or alert dialog to inform user
            },
            onPatientUpdated(patient_id) {
                getPatientById(patient_id).then(response => {
                    const {data} = response.data
                    console.log(data)

                    this.updatePatientTable(patient_id, data)
                })
            },
            updatePatientTable(patient_id, payload) {
                this.patients.forEach(p => {
                    if (p.id === patient_id) {
                        p.ph_code = payload.ph_code
                        p.wv_code = payload.wv_code
                        p.sex = payload.sex
                        p.age = payload.age
                        p.citymunCode = payload.citymunCode
                        p.citymunDesc = payload.citymunDesc
                        p.provCode = payload.provCode
                        p.provDesc = payload.provDesc
                        p.places_visited = payload.places_visited
                        p.date_illness_onset = payload.date_illness_onset
                        p.signs_and_symptoms = payload.signs_and_symptoms
                        p.date_consulted = payload.date_consulted
                        p.lab_result_desc = payload.lab_result_desc
                        p.lab_result_date = payload.lab_result_date
                        p.hospitalization_desc = payload.hospitalization_desc
                        p.hospitalization_date = payload.hospitalization_date
                    }
                })
            }

        }
    }
</script>

<style scoped>

</style>