<template>
    <div>
        <v-data-table
                :headers="headers"
                :items="patients"
                :options.sync="options"
                :loading="loading"
                class="elevation-1"
                :footer-props="footer_props"
                :fixed-header="true"
        >
            <template v-slot:top>
                <v-toolbar>
                    <v-toolbar-title>{{$vuetify.breakpoint.xs ? 'PUI' : 'Patients under Investigation'}}
                    </v-toolbar-title>
                    <v-divider
                            class="mx-4"
                            inset
                            vertical
                    ></v-divider>
                    <v-spacer></v-spacer>
                    <v-btn v-if="$_authMixin_isauthenticated" color="secondary" dark class="mb-2" @click="add_pui">Add
                        PUI
                    </v-btn>
                </v-toolbar>
            </template>
            <template v-slot:item.sex="{ item }">
                <v-chip :color="getAgeColor(item.sex)" dark>{{ item.sex }}</v-chip>
            </template>
            <template v-slot:item.age="{ item }">
                {{ item.age }}
                <v-tooltip right>
                    <template v-slot:activator="{ on }">
                        <v-icon color="warning" v-if="isAgeAtRisk(item.age)" v-on="on">mdi-alert-circle</v-icon>
                    </template>
                    <span>Age is at Risk</span>
                </v-tooltip>
            </template>
            <template v-slot:item.lab_result_desc="{ item }">
                <v-chip v-if="item.lab_result_desc !== ''" :color="getLabColor(item.lab_result_desc)" dark>{{
                    item.lab_result_desc }}
                </v-chip>
            </template>
            <template v-slot:item.actions="{ item }">
                <v-row no-gutters align="start" justify="start">
                    <v-tooltip bottom>
                        <template v-slot:activator="{ on }">
                            <v-icon
                                    class="ma-2"
                                    v-on="on"
                                    @click="view_pui(item)"
                            >
                                mdi-file-find
                            </v-icon>
                        </template>
                        <span>View PUI Profile</span>
                    </v-tooltip>
                    <v-tooltip bottom>
                        <template v-slot:activator="{ on }">
                            <v-icon
                                    class="ma-2"
                                    @click="view_lab(item.id)"
                                    v-on="on"
                            >
                                mdi-stethoscope
                            </v-icon>
                        </template>
                        <span>Lab Results</span>
                    </v-tooltip>
                    <v-tooltip bottom>
                        <template v-slot:activator="{ on }">
                            <v-icon
                                    class="ma-2"
                                    @click="view_hospitalization(item.id)"
                                    v-on="on"
                            >
                                mdi-calendar-check
                            </v-icon>
                        </template>
                        <span>Hospitalization</span>
                    </v-tooltip>
                </v-row>
            </template>
        </v-data-table>
    </div>
</template>

<script>
    import {authMixin} from "../../mixins/auth"

    export default {
        name: "PuiTable",
        mixins: [authMixin],
        props: {
            loading: Boolean,
            patients: Array,
            // totalPatients: Number,
            // page: Number,
            // itemPerPage: {
            //     type: Number,
            //     default: 10
            // }
        },
        data() {
            return {
                headers: [
                    {text: '#', value: 'id'},
                    {text: 'WV Code', value: 'wv_code'},
                    {text: 'Sex', value: 'sex'},
                    {text: 'Age', value: 'age'},
                    {text: 'Province', value: 'provDesc'},
                    {text: 'City/Municipality', value: 'citymunDesc'},
                    // {text: 'Signs & Symptoms', value: 'signs_and_symptoms'},
                    {text: 'Lab Result', value: 'lab_result_desc'},
                    {text: 'Hospitalization', value: 'hospitalization_desc'},
                    {text: 'Actions', value: 'actions'},
                ],
                footer_props: {
                    'items-per-page-options': [10, 50, 100, -1]
                },
                options: {
                    itemsPerPage: 10,
                    page: 1
                },
            }
        },
        methods: {
            syncPropsToLocalData() {
                this.options.itemsPerPage = this.itemPerPage
                this.options.page = this.page
            },
            getAgeColor(sex) {
                switch (sex) {
                    case 'MALE':
                        return 'light-blue darken-2'
                    case 'FEMALE':
                        return 'pink lighten-1'
                    default:
                        return 'grey'
                }
            },
            getLabColor(lab) {
                switch (lab) {
                    case 'POSITIVE':
                        return 'blue darken-3'
                    case 'NEGATIVE':
                        return 'orange darken-3'
                    case 'PENDING':
                        return 'yellow darken-3'
                    default:
                        return 'grey'
                }
            },
            isAgeAtRisk(age) {
                return age >= 55
            },
            view_pui(item) {
                this.$emit('view-pui', item)
            },
            view_lab(id) {
                this.$emit('view-lab', id)
            },
            view_hospitalization(id) {
                this.$emit('view-hospitalization', id)
            },
            add_pui() {
                this.$emit('add-pui')
            }
        },
        watch: {
            options: {
                handler(n) {
                    console.log(n)
                    const {page, itemsPerPage} = n
                    let sortBy = 'id', direction = 'asc'

                    if (n.sortBy.length > 0) {
                        sortBy = n.sortBy[0]
                    }

                    if (n.sortDesc.length > 0) {
                        direction = n.sortDesc[0] ? 'desc' : 'asc'
                    }

                    console.log(sortBy)

                    this.$emit('table-options-updated', {page, itemsPerPage, sortBy, direction})
                },
                deep: true
            }
        }
    }
</script>

