<template>
    <v-row class="fill-height" no-gutters>
        <v-col>
            <v-card id="my-toolbar-container">
                <v-toolbar id="my-toolbar" dark color="primary">
                    <v-btn icon dark @click="$emit('dialog-close')">
                        <v-icon>mdi-close</v-icon>
                    </v-btn>
                    <v-toolbar-title>PUI Profile : {{patient_id}}</v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-btn v-if="$_authMixin_isauthenticated" color="secondary" @click="edit">Edit</v-btn>
                </v-toolbar>
                <v-card-text>
                    <v-container>
                        <v-row>
                            <v-col cols="6">
                                <v-text-field label="PH Code" placeholder="PH ##" :value="ph_code"
                                              readonly></v-text-field>
                            </v-col>
                            <v-col cols="6">
                                <v-text-field label="WV Code" placeholder="WV ##" :value="wv_code"
                                              readonly></v-text-field>
                            </v-col>
                            <v-col cols="6">
                                <v-text-field label="Sex*" placeholder="MALE/FEMALE/OTHERS" :value="sex"
                                              readonly></v-text-field>
                            </v-col>
                            <v-col cols="6">
                                <v-text-field label="Age*" placeholder="#" type="number" :value="age"
                                              readonly></v-text-field>
                            </v-col>
                            <v-col cols="6">
                                <v-text-field label="Province*" placeholder="" :value="province"
                                              readonly></v-text-field>
                            </v-col>
                            <v-col cols="6">
                                <v-text-field label="City/Municipality*" placeholder="" :value="citymun"
                                              readonly></v-text-field>
                            </v-col>
                            <v-col cols="12">
                                <v-textarea label="Places Visited" outlined :value="places_visited"
                                            readonly></v-textarea>
                            </v-col>
                            <v-col cols="12">
                                <v-text-field label="Date Illness Onset" :value="date_illness_onset"
                                              readonly></v-text-field>
                            </v-col>
                            <v-col cols="12">
                                <v-textarea label="Signs & Symptoms" :value="signs_and_symptoms" outlined
                                            readonly></v-textarea>
                            </v-col>
                            <v-col cols="12">
                                <v-text-field label="Date Consulted*" :value="date_consulted"
                                              readonly></v-text-field>
                            </v-col>
                        </v-row>
                    </v-container>
                    <small>*indicates required field</small>
                </v-card-text>
            </v-card>
        </v-col>

    </v-row>
</template>

<script>
    import {authMixin} from "../../mixins/auth"

    export default {
        name: "PuiViewForm",
        mixins: [authMixin],
        props: {
            patient_id: Number,
            ph_code: String,
            wv_code: String,
            sex: String,
            age: Number,
            province: String,
            citymun: String,
            places_visited: String,
            date_illness_onset: String,
            signs_and_symptoms: String,
            date_consulted: String
        },
        methods: {
            edit() {
                this.$emit('pui-edit', this.patient_id)
            }
        }
    }
</script>

<style scoped>

</style>