<template>
    <v-row class="fill-height" no-gutters>
        <v-col>
            <v-card>
                <v-toolbar dark color="secondary">
                    <v-btn icon dark @click="$emit('dialog-close')">
                        <v-icon>mdi-close</v-icon>
                    </v-btn>
                    <v-toolbar-title>Edit PUI Profile : {{patient_id}}</v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-btn v-if="$_authMixin_isauthenticated" color="accent" @click="save">Save</v-btn>
                </v-toolbar>
                <v-card-text>
                    <v-container>
                        <v-row>
                                    <v-col cols="12" v-if="error">
                                        <v-alert v-for="(error, i) in errorMsgs" :key="i" type="error">
                                            {{error}}
                                        </v-alert>
                                    </v-col>
                            <v-col cols="6">
                                <v-text-field label="PH Code" placeholder="PH ##" v-model="ph_code"></v-text-field>
                            </v-col>
                            <v-col cols="6">
                                <v-text-field label="WV Code" placeholder="WV ##" v-model="wv_code"></v-text-field>
                            </v-col>
                            <v-col cols="6">
                                <v-select
                                        :items="['MALE', 'FEMALE', 'OTHERS']"
                                        label="Sex*"
                                        placeholder="MALE/FEMALE/OTHERS"
                                        required
                                        v-model="sex"
                                ></v-select>
                            </v-col>
                            <v-col cols="6">
                                <v-text-field label="Age*" placeholder="#" type="number" v-model="age"></v-text-field>
                            </v-col>
                            <v-col cols="6">
                                <v-select
                                        :items="provinces"
                                        label="Province*"
                                        required
                                        item-text="provDesc"
                                        item-value="provCode"
                                        v-model="selectedProvince"
                                ></v-select>
                            </v-col>
                            <v-col cols="6">
                                <v-select
                                        :items="citymuns"
                                        label="City/Municipality*"
                                        required
                                        item-text="citymunDesc"
                                        item-value="citymunCode"
                                        v-model="selectedCitymun"
                                ></v-select>
                            </v-col>
                            <v-col cols="12">
                                <v-textarea label="Places Visited" outlined v-model="places_visited"></v-textarea>
                            </v-col>
                            <v-col cols="12">
                                <v-menu
                                        v-model="date_illness_onset_menu"
                                        :close-on-content-click="false"
                                        transition="scale-transition"
                                        offset-y
                                        min-width="290px"
                                >
                                    <template v-slot:activator="{ on }">
                                        <v-text-field
                                                v-model="date_illness_onset"
                                                label="Date Illness Offset"
                                                readonly
                                                v-on="on"
                                        ></v-text-field>
                                    </template>
                                    <v-date-picker v-model="date_illness_onset" @input="date_illness_onset_menu = false" no-title scrollable>
                                    </v-date-picker>
                                </v-menu>
                            </v-col>
                            <v-col cols="12">
                                <v-textarea label="Signs & Symptoms" outlined v-model="signs_and_symptoms"></v-textarea>
                            </v-col>
                            <v-col cols="12">
                                <v-menu
                                        v-model="date_consulted_menu"
                                        :close-on-content-click="false"
                                        transition="scale-transition"
                                        offset-y
                                        min-width="290px"
                                >
                                    <template v-slot:activator="{ on }">
                                        <v-text-field
                                                v-model="date_consulted"
                                                label="Date Consulted*"
                                                readonly
                                                v-on="on"
                                        ></v-text-field>
                                    </template>
                                    <v-date-picker v-model="date_consulted" @input="date_consulted_menu = false" no-title scrollable>
                                    </v-date-picker>
                                </v-menu>
                            </v-col>

                        </v-row>
                    </v-container>
                    <small>*indicates required field</small>
                </v-card-text>
            </v-card>
        </v-col>

    </v-row>
</template>

<script>
    import {prov, gis} from "../../util/gis"
    import {editPatient, getPatientById} from "../../util/api"
    import {authMixin} from "../../mixins/auth"

    export default {
        name: "PuiAddForm",
        mixins: [authMixin],
        props: {
          patient_id: Number
        },
        created() {
          this.loadPatient()
        },
        computed: {
            provinces() {
                return prov.data
            },
            citymuns() {
                if (this.selectedProvince !== '') {
                    const item = gis.data.filter(i => {
                        return i.provCode === this.selectedProvince
                    })

                    return item[0].citymuns
                } else {
                    return []
                }
            },
        },
        data() {
            return {
                ph_code: '',
                wv_code: '',
                sex: '',
                age: 0,
                selectedProvince: '',
                selectedCitymun: '',
                places_visited: '',
                date_illness_onset: '',
                signs_and_symptoms: '',
                date_consulted: '',

                date_illness_onset_menu: false,
                date_consulted_menu: false,


                loading: false,
                disabled: false,
                error: false,
                errorMsgs: [],
            }
        },
        methods: {
            loadPatient() {
                getPatientById(this.patient_id).then(response => {
                    console.log(response.data)
                    const {data} = response.data

                    this.ph_code = data.ph_code
                    this.wv_code = data.wv_code
                    this.sex = data.sex
                    this.age = data.age
                    this.selectedProvince = data.provCode
                    this.selectedCitymun = data.citymunCode
                    this.places_visited = data.places_visited
                    this.date_illness_onset = data.date_illness_onset
                    this.signs_and_symptoms = data.signs_and_symptoms
                    this.date_consulted = data.date_consulted

                }).catch(error => {
                    this.$emit('dialog-close')
                })
            },
            save() {
                this.loading = true
                this.errorMsgs = []
                const {
                    ph_code,
                    wv_code,
                    sex,
                    age,
                    selectedCitymun,
                    places_visited,
                    date_illness_onset,
                    signs_and_symptoms,
                    date_consulted
                } = this

                const payload = {
                    ph_code,
                    wv_code,
                    sex,
                    age,
                    citymunCode: selectedCitymun,
                    places_visited,
                    date_illness_onset,
                    signs_and_symptoms,
                    date_consulted
                }

                editPatient(this.patient_id, payload).then(res => {
                    this.error = false
                    this.loading = false

                    const {
                        id,
                        ph_code,
                        wv_code,
                        sex,
                        age,
                        citymunCode,
                        places_visited,
                        date_illness_onset,
                        signs_and_symptoms,
                        date_consulted
                    } = res.data.data

                    const fixedData = {
                        id,
                        ph_code,
                        wv_code,
                        sex,
                        age: parseInt(age),
                        citymunCode,
                        places_visited,
                        date_illness_onset,
                        signs_and_symptoms,
                        date_consulted
                    }


                    this.$emit('pui-edited', fixedData)
                    this.$emit('dialog-close')
                }).catch(error => {
                    this.error = true
                    this.loading = false

                    if(error.response) {
                        console.log(error.response)
                        const {data} = error.response

                        if (data.errors) {

                            Object.keys(data.errors).forEach(key => {
                                const field_errors = data.errors[key]
                                this.errorMsgs.push(...field_errors)
                            });
                        } else {
                            this.errorMsgs = ['Unauthorised to perform this action']
                        }
                    } else if(error.request) {
                        this.errorMsgs = ['Cannot connect to server. Please try again.']
                    } else {
                        this.errorMsgs = ["Application Error"]
                    }
                })
            }
        }
    }
</script>

