<template>
    <v-row class="fill-height" no-gutters>
        <v-col>
            <v-card>
                <v-toolbar dark color="primary">
                    <v-btn icon dark @click="$emit('dialog-close')">
                        <v-icon>mdi-close</v-icon>
                    </v-btn>
                    <v-toolbar-title>PUI Hospitalizations</v-toolbar-title>
                    <v-spacer></v-spacer>
                </v-toolbar>
                <v-card-text>
                    <v-container>
                        <v-row>
                            <v-col cols="12">
                                <v-simple-table>
                                    <template v-slot:default>
                                        <thead>
                                        <tr>
                                            <th class="text-left">Date</th>
                                            <th class="text-left">Hospitalization</th>
                                        </tr>
                                        </thead>
                                        <tbody>
                                        <tr v-for="item in hospitalizations" :key="item.id">
                                            <td>{{ item.date }}</td>
                                            <td>{{ item.hospitalization }}</td>
                                        </tr>
                                        </tbody>
                                    </template>
                                </v-simple-table>
                            </v-col>
                            <v-col cols="12" v-if="$_authMixin_isauthenticated">
                                <v-row>
                                    <v-col cols="6">
                                        <v-menu
                                                v-model="date_menu"
                                                :close-on-content-click="false"
                                                transition="scale-transition"
                                                offset-y
                                                min-width="290px"
                                        >
                                            <template v-slot:activator="{ on }">
                                                <v-text-field
                                                        v-model="date"
                                                        label="Date of Hospitalization*"
                                                        placeholder="Select Date"
                                                        readonly
                                                        v-on="on"
                                                ></v-text-field>
                                            </template>
                                            <v-date-picker v-model="date" @input="date_menu = false" no-title scrollable>
                                            </v-date-picker>
                                        </v-menu>
                                    </v-col>
                                    <v-col cols="6">
                                        <v-select
                                                :items="['ADMITTED', 'OPD', 'DISCHARGED', 'DIED', 'HOME QUARANTINE', 'TBA']"
                                                label="Hospitalization*"
                                                placeholder="ADMITTED/OPD/DISCHARGED/DIED/HOME QUARANTINE/TBA"
                                                required
                                                v-model="hospitalization"
                                        ></v-select>
                                    </v-col>
                                    <v-col cols="12" class="d-flex justify-end">
                                        <v-btn color="primary" @click="add" :loading="loading" :disabled="loading">Add</v-btn>
                                    </v-col>
                                    <v-col cols="12" v-if="error">
                                        <v-alert v-for="(error, i) in errorMsgs" :key="i" type="error">
                                            {{error}}
                                        </v-alert>
                                    </v-col>
                                </v-row>
                            </v-col>
                        </v-row>
                    </v-container>
                </v-card-text>
            </v-card>
        </v-col>
    </v-row>
</template>

<script>
    import {addHospitalization, getPatientHospitalizations} from "../../util/api"
    import {authMixin} from "../../mixins/auth"

    export default {
        name: "PuiLabResultTable",
        mixins: [authMixin],
        props: {
            patient_id: Number
        },
        created() {
            this.loadApi()
        },
        data() {
            return {
                hospitalizations: [],
                date: '',
                hospitalization: '',

                date_menu: false,
                loading: false,
                error: false,
                errorMsgs: []
            }
        },
        methods: {
            loadApi() {
                getPatientHospitalizations(this.patient_id).then(res => {
                    const {data} = res.data
                    this.hospitalizations = data
                }).catch(error => {

                })
            },
            add() {
                this.loading = true
                this.errorMsgs = []

                const payload = {
                    patient_id: this.patient_id,
                    hospitalization: this.hospitalization,
                    date: this.date
                }

                addHospitalization(payload).then(res => {
                    this.error = false
                    this.loading = false

                    const {data} = res.data

                    this.hospitalization = ''
                    this.date = ''
                    this.hospitalizations.push(data)

                    this.$emit('hospitalizations-updated', this.patient_id)
                }).catch(error => {
                    this.error = true
                    this.loading = false

                    if(error.response) {
                        console.log(error.response)
                        const {data} = error.response

                        if (data.errors) {

                            Object.keys(data.errors).forEach(key => {
                                const field_errors = data.errors[key]
                                this.errorMsgs.push(...field_errors)
                            });
                        } else {
                            this.errorMsgs = ['Unauthorised to perform this action']
                        }
                    } else if(error.request) {
                        this.errorMsgs = ['Cannot connect to server. Please try again.']
                    } else {
                        this.errorMsgs = ["Application Error"]
                    }
                })
            }
        }
    }
</script>

